}

body.is-open {
  overflow: hidden;
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}

img {
  display: block;
}

:root {
  --color-primary: #fff;
  --color-invert: #000;
  --color-accept: #ff001b;
  --color-text-deck: #ff6b08;
  --background-action-WATCHING: #ff6b01;
  --background-action-QUEUE: #ff6b02;
  --color-name-data: #8c8c8c;
  --color-text-footer: #545454;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

.container {
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.header {
  background-image: url("header-library-mobile.ed45b3f6.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 40px;
  padding-bottom: 60px;
}

@media screen and (min-width: 768px) {
  .header {
    background-image: url("header-library-tablet.891bf7af.png");
  }
}

@media screen and (min-width: 1200px) {
  .header {
    background-image: url("header-library-desktop.8a1b0dc2.png");
  }
}

.nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav-list {
  display: flex;
}

.nav-item:not(:last-child) {
  margin-right: 40px;
}

.title-desktop {
  display: none;
}

.nav-link {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 2;
  display: block;
  position: relative;
}

.nav-link.menu-line.current:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: var(--color-accept);
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media screen and (min-width: 768px) {
  .title-desktop {
    color: #fff;
    text-align: center;
    margin-left: 8px;
    font-size: 30px;
    font-weight: 500;
    display: inline-block;
  }
}

.multiple-container {
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  display: flex;
}

.btn-filter {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 14px 36px;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  transition: background-color .25s linear, border .25s linear;
}

.btn-filter:hover, .btn-filter:focus, .btn-filter:active {
  filter: drop-shadow(0 8px 43px rgba(255, 107, 1, .6));
  background-color: #ff6b01;
  border: 1px solid #ff6b01;
}

.btn-queue {
  padding: 14px 45px;
}

.btn-watched:not(:last-of-type) {
  margin-right: 20px;
}

@media screen and (min-width: 768px) {
  .btn-watched:not(:last-of-type) {
    margin-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .btn-watched:not(:last-of-type) {
    margin-right: 16px;
  }
}

